/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Karla", sans-serif;
  font-size: 16px;
}

body {
  background-color: hsl(204deg, 43%, 93%);
  display: flex;
  justify-content: center;
  align-items: center;
}

main {
  margin: 10vh 0;
  width: calc(100% - 64px);
  border-radius: 5px;
  box-shadow: 0 10px 20px 3px hsla(0deg, 0%, 0%, 0.1);
}
main .description {
  background-color: hsl(0deg, 0%, 100%);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 2rem 1.5rem;
}
main .description h1 {
  font-weight: 700;
  color: hsl(179deg, 62%, 43%);
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
}
main .description h3 {
  font-weight: 700;
  color: hsl(71deg, 73%, 54%);
  font-size: 1rem;
  margin-bottom: 1rem;
}
main .description p {
  font-weight: 400;
  color: hsl(218deg, 22%, 67%);
  font-size: 0.85rem;
  line-height: 1.5rem;
}
main .container {
  display: flex;
  flex-direction: column;
}
main .container h2 {
  font-weight: 700;
  color: hsl(204deg, 43%, 93%);
  font-size: 1.1rem;
  margin-bottom: 1rem;
}
main .container .subscribe {
  background-color: hsl(179deg, 62%, 43%);
  padding: 1.5rem;
}
main .container .subscribe .price {
  display: flex;
  align-items: center;
  font-weight: 400;
  color: rgba(229, 239, 245, 0.6);
  margin-bottom: 0.5rem;
}
main .container .subscribe .price span {
  font-weight: 700;
  font-size: 2rem;
  color: hsl(204deg, 43%, 93%);
  margin-right: 0.8rem;
}
main .container .subscribe .deal {
  color: hsl(204deg, 43%, 93%);
  margin-bottom: 2rem;
}
main .container .subscribe a {
  display: block;
  text-align: center;
  width: 100%;
  background-color: hsl(71deg, 73%, 54%);
  padding: 1rem;
  border-radius: 5px;
  text-decoration: none;
  color: hsl(0deg, 0%, 100%);
  box-shadow: 0 5px 10px 5px hsla(0deg, 0%, 0%, 0.1);
  transition: background-color 0.1s ease-in-out;
  cursor: pointer;
}
main .container .subscribe a:hover {
  background-color: #a4c21e;
}
main .container .why-us {
  background-color: hsl(179deg, 47%, 52%);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 1.5rem;
}
main .container .why-us ul {
  list-style-type: none;
}
main .container .why-us ul li {
  font-size: 0.9rem;
  line-height: 1.2rem;
  color: rgba(229, 239, 245, 0.7);
}

@media screen and (min-width: 675px) {
  body {
    height: 100vh;
  }
  main {
    margin: 0;
    max-width: 635px;
  }
  main .description {
    padding: 2.5rem;
  }
  main .description h1 {
    font-size: 1.5rem;
  }
  main .description h3 {
    font-size: 1.1rem;
  }
  main .description p {
    font-size: 1rem;
  }
  main .container {
    flex-direction: row;
  }
  main .container .subscribe, main .container .why-us {
    flex: 1;
    padding: 2.5rem;
  }
  main .container .subscribe {
    border-bottom-left-radius: 5px;
  }
  main .container .why-us {
    border-bottom-left-radius: 0;
  }
  main .container .why-us ul li {
    font-size: 0.95rem;
    line-height: 1.3rem;
  }
}